.section {
  margin-top: 70px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background-color: rgb(229, 231, 233);
}
.container {
}
.imageContainer {
  margin-top: 70px;
  width: 100%;
  height: 20rem;
}

.image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.titleContainer {
}
.title {
  font-family: 'Roboto', sans-serif;
}
.content{
  font-family: 'Open Sans', sans-serif;
}

.descriptionContainer {
}
.linkContainer {
}
.link {
  font-size: 3rem;
  color: black;
}

@media screen and (min-width: 500px) {
  .container {
    width: 30rem;
  }
}

@media screen and (min-width: 800px) {
  .container {
    width: 50rem;
  }
}
